import { useState, useEffect } from "react";
import { EventFormData } from "../formData/EventFormData";
import { TicketEvent } from "../interfaces/TicketEvent";
import { validateEventField } from "../services/validator/eventValidator";

export const useEventForm = (initialData?: TicketEvent) => {
  const [formData, setFormData] = useState<EventFormData>({
    name: "",
    description: "",
    minAge: "",
    date: "",
  });

  const [errors, setErrors] = useState<Record<keyof EventFormData, string>>({
    name: "",
    description: "",
    minAge: "",
    date: "",
  });
  const rawDate = initialData?.date;


if (rawDate && rawDate !== "Not stated") {
  const parts = rawDate.split("/");
  if (parts.length === 3) {
    const month = parseInt(parts[0], 10);
    const day = parseInt(parts[1], 10);
    const year = parseInt(parts[2], 10);

    const dateObj = new Date(year, month - 1, day);
    initialData.date = dateObj.toISOString().slice(0, 16); 
  }
}


  useEffect(() => {
    if (initialData) {
            console.log("ininital", initialData);


      setFormData({
        name: String(initialData.name || ""),
        description: String(initialData.description || ""),
        minAge: String(initialData.minAge || ""),
        date: initialData.date || "",
      });
    } else {
      setFormData({
        name: "",
        description: "",
        minAge: "",
        date: "",
      });
    }
  }, [initialData]);

  const handleChange = (field: keyof EventFormData, value: any) => {
    const safeValue = value === undefined ? "" : value;

    setFormData((prev) => ({
      ...prev,
      [field]: safeValue,
    }));

    const error = validateEventField(field, safeValue);
    setErrors((prev) => ({ ...prev, [field]: error }));
  };

  const validateForm = (): boolean => {
    const newErrors = {
      name: validateEventField("name", formData.name),
      description: validateEventField("description", formData.description),
      minAge: validateEventField("minAge", formData.minAge),
      date: validateEventField("date", formData.date),
    };

    setErrors(newErrors);
    return !Object.values(newErrors).some((error) => error !== "");
  };

  const getSubmitData = () => {
    let formattedDate = null;
    if (formData.date) {
      const date = new Date(formData.date);
      formattedDate = date.toISOString();
    }

    return {
      name: formData.name.trim(),
      description: formData.description.trim() || null,
      minAge: formData.minAge ? parseInt(formData.minAge) : null,
      date: formattedDate,
    };
  };

  return {
    formData,
    errors,
    handleChange,
    validateForm,
    getSubmitData,
    isFormValid:
      !formData.name || Object.values(errors).some((error) => error !== ""),
  };
};
